.nav {
  width: 6%;
  background-color: #0077bd;
  padding: 10px;
  .list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    a {
      color: #fff;
      transition: all 0.3s linear;
      &:hover {
        color: #fff;
      }
    }
    a + a {
      margin-top: 5px;
    }
  }
}
